'use client'

import {useState} from 'react'

export default function KrCenter() {
    const [input, setInput] = useState<string>('')
    const [encoded, setEncoded] = useState<Record<string, string>>({
        encoded: '',
        urlEncoded: ''
    })
    const [decoded, setDecoded] = useState<Record<string, string>>({
        decoded: '',
        urlDecoded: ''
    })

    // Base64 인코딩 함수
    const handleEncode = () => {
        setEncoded({
            encoded: btoa(input),
            urlEncoded: makeUrlSafe(btoa(input))
        })
        // 디코딩된 값 초기화
        setDecoded({})
    }

    // Base64 디코딩 함수
    const handleDecode = () => {
        setDecoded({
            decoded: atob(input),
            urlDecoded: atob(makeUrlUnsafe(input))
        })
        // 인코딩된 값 초기화
        setEncoded({})
    }

    // URL-safe Base64 변환 함수 (인코딩)
    const makeUrlSafe = (base64String: string) => {
        return base64String.replace(/\+/g, '-').replace(/\//g, '_').replace(/=+$/, '')
    }

    // URL-safe Base64 변환 함수 (디코딩)
    const makeUrlUnsafe = (urlSafeString: string) => {
        let base64String = urlSafeString.replace(/-/g, '+').replace(/_/g, '/')
        // 패딩을 추가해야 하는 경우 처리
        while (base64String.length % 4 !== 0) {
            base64String += '='
        }

        return base64String
    }

    return (
        <div style={{padding: "20px", maxWidth: "600px", margin: "0 auto"}}>
            <h1>Base64 Encoder/Decoder</h1>

            <textarea
                value={input}
                onChange={(e) => setInput(e.target.value.trim())}
                rows={4}
                placeholder="Enter text to encode or Base64 string to decode"
                style={{width: "100%", padding: "10px", marginBottom: "10px"}}
            />

            <div>
                <button onClick={handleEncode} style={{marginRight: "10px"}}>Encode</button>
                <button onClick={handleDecode}>Decode</button>
            </div>

            {/* Encoding result */}
            {encoded.encoded && (
                <div style={{marginTop: "20px"}}>
                    <h2>Encoded Result</h2>
                    <pre style={{backgroundColor: "#f0f0f0", padding: "10px"}}>
                      <strong style={{color: "#333", display: "block", marginBottom: "8px"}}>
                        Base64 Encoded:
                      </strong>
                        {encoded.encoded}

                        <br/><br/>

                      <strong style={{color: "#333", display: "block", marginBottom: "8px"}}>
                        Base64 URL-Safe Encoded:
                      </strong>
                        {encoded.urlEncoded}
                    </pre>
                </div>
            )}

            {/* Decoding result */}
            {decoded.decoded && (
                <div style={{marginTop: "20px"}}>
                    <h2>Encoded Result</h2>
                    <pre style={{backgroundColor: "#f0f0f0", padding: "10px"}}>
                      <strong style={{color: "#333", display: "block", marginBottom: "8px"}}>
                        Base64 Encoded:
                      </strong>
                        {decoded.decoded}

                        <br/><br/>

                      <strong style={{color: "#333", display: "block", marginBottom: "8px"}}>
                        Base64 URL-Safe Encoded:
                      </strong>
                        {decoded.urlDecoded}
                    </pre>
                </div>
            )}
        </div>
    )
}